* {                   
    box-sizing: border-box;
}
  
body {
    margin: 0;
    padding: 0;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
input {
    -moz-user-select: none; 
    -ms-user-select: none; 
    -o-user-select: none;
    -webkit-user-select: none;
    -webkit-appearance: none;
}
  
input:active, input:focus {
    outline: none;
}
  
button {
    display: inline-block;
    padding: 0;
    border: 0;
    -moz-user-select: none;
    -ms-user-select: none; 
    -o-user-select: none; 
    -webkit-user-select: none;
    cursor: pointer;
}
  
button:active, button:focus {
    outline: none;
}
  
button::-moz-focus-inner {
    border: 0;
}
  
ul {
    list-style-type: none;
    margin: 0;
    padding-left: 25px;
}
  
textarea {
    outline: none;
    -moz-appearance: none;
    -webkit-appearance: none;
    border: none;
    font-family: "Verdana", "Tahoma", "Arial", sans-serif;
}
.EditableItem {
    height: 25px;
}

.EditableItem-Text {
    height: 100%;
    width: 150px;
    border: none;
    padding: 10px;
    background-color: white;
    border-bottom: 1px solid whitesmoke;
    font-size: 10pt;
}

.EditableItem-Button {
    height: 25px;
    width: 25px;
    background-color: white;
    border-right: 1px solid whitesmoke;
    border-bottom: 1px solid whitesmoke;
    color: grey;
    font-size: 10pt;
}

.EditableItem-Button_add:hover {
    color: black;
}

.EditableItem-Button_remove:hover {
    color: orangered;
}
.TreeNode {
    background: none;
    margin: 3px;
}
.AddButton {
    height: 70px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    margin-top: 5px;
}

.AddButton-Inner {
    height: 40px;
    width: 40px;
    border-radius: 100%;
    font-size: 18pt;
    margin-left: 30px;
    background-color: white;
    border: 1px solid gainsboro;
}

.AddButton-Inner:hover:active {
    background-color: rgb(250, 250, 250);    
    border: 1px solid rgb(235, 235, 235);
}
.ControlPanel {
    height: 40px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center
}

.ControlPanel-Button {
    margin-left: 25px;
    background-color: white;
    font-size: 12pt;
    color: dodgerblue;
    height: 30px;
    border-radius: 5px;

}

.ControlPanel-Button:hover:active {
    opacity: .4;
}

.ControlPanel-Button_load {
    color: mediumturquoise;
}
.TextView {
    width: 100%;
    height: 100vh;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}

.TextView-Area {
    resize: none;
    width: 90%;
    height: 90%;
    padding-left: 25px;
    color: dimgrey;
    font-size: 10pt;
    border-left: 1px solid gainsboro;
}
.Tree {
    display: -webkit-flex;
    display: flex;
    width: 100%;
    min-width: 800px;
}

.Tree-LeftSide {
    width: 65%;
}

.Tree-RightSide {
    width: 35%;
}
